
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
export default {
data() {
return {
lasttype: '', //上页type
lastshow: [], //上页显示模块
show: { //显示模块
1: false,
2: false,
3: false,
4: false,
5: false,
6: false,
7: false,
8: false,
9: false,
10: false,
},
close: { //展开模块
1: true,
2: true,
3: true,
4: true,
5: true,
6: true,
7: true,
8: true,
9: true,
10: true,
},
objdata: {

data1: {
ZZMC: ''
},

data2: {},

data3: {},

data4: {},

data5: {},

data6: {},

data7: {},

data8: {
ZYS: [1],
JTBCS: [1],
YWQK: []
},

data9: {
YFJZ: []
},

data10: {
JKPJMS: '',
WXYSKZMC: ''
},
}
}
},
components: {
XHeader,
Collapse
},
mounted() {
this.load();
this.initdata(false); 
},
methods: {

load() {
this.lasttype = this.$route.params.type;
this.lastshow = this.$route.params.showtype;
let self = this;
console.log(this.lastshow, this.show)
for (let i = 0; i < self.lastshow.length; i++) {
const element = self.lastshow[i];
self.show[element] = true; 
}
this.close[this.lasttype] = false; 
},

initdata(type, conShow) {
console.log(conShow)
if (!type) {
type = this.lasttype
}
if (typeof conShow != 'undefined' && !conShow) { //关闭展开 不请求
return false
}
this.$ajax.post('', {
'MSH.1': 'tjjl',
'MSH.2': 'qryTJDetail',
'TJJLID': this.$route.params.code, //'000223887';
'type': type,
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(type)
let self = this;
if (typeof data != 'undefined' && Object.keys(data).length > 0) { //判断是否空对象

let newobj = Object.assign({}, this.objdata['data' + type], data);
this.objdata['data' + type] = newobj;
}
console.log(this.objdata['data' + type])
})
},
}
}



